import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { MyProfileService } from './../my-profile.service';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { MatDialog, MatDialogRef } from '@angular/material';
import { EditProfileComponent } from '../edit-profile/edit-profile.component';
import { ToasterService } from '@fuse/services/toaster-service.service';
import * as moment from 'moment';

@Component({
  selector: 'app-my-profile',
  templateUrl: './my-profile.component.html',
  styleUrls: ['./my-profile.component.scss'],
  animations   : fuseAnimations
})
export class MyProfileComponent implements OnInit {
  dialogRef: any;
  confirmDialogRef: MatDialogRef<EditProfileComponent>;
  client_id: string;
  client: any;

  constructor(public dialog: MatDialog,
    private apiloader: ApiLoaderService,
    private toast: ToasterService,
    private myService: MyProfileService) { 
        this.client_id = sessionStorage.getItem('client_id'); 
    }

  ngOnInit() {
      this.getClientDetails();    
  }

  editProfile()
  {
      this.dialogRef = this.dialog.open(EditProfileComponent, {
          panelClass: 'myProfile',
          data      : {
            client: this.client
          }
      });

      this.dialogRef.afterClosed()
          .subscribe(response => {
              if ( !response ) { return; }
              else{
                this.updateClient(response);
              }
          });
  }

  getClientDetails(){
    this.apiloader.setLoader(true);
    this.myService.getData('clients/' + this.client_id).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.client = response.data;
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
  }

  updateClient(response){
    this.apiloader.setLoader(true);
    const formData = response[1];
    const uploader = response[0];
    const sdate = moment(formData.start_date).format('YYYY-MM-DD');

    let form = new FormData();
    form.append('logo', formData.logo);
    form.append('company_name', formData.company_name);
    form.append('description', formData.description);
    form.append('vision', formData.vision);
    form.append('mission', formData.mission);
    form.append('company_values', formData.company_values);
    form.append('start_date', sdate);
    form.append('uen', formData.uen);
    form.append('tax_number', formData.tax_number);
    form.append('address', formData.address);
    form.append('contact_number', formData.contact_number);
    form.append('website', formData.website);
    form.append('email', formData.email);

    /* const body = new URLSearchParams();
    body.set('company_name', formData.company_name);
    body.set('email', formData.email);
    body.set('mission', formData.mission);
    body.set('vision', formData.vision);
    body.set('logo', formData.logo);
    body.set('company_values', formData.company_values);
    body.set('description', formData.description);
    body.set('uen', formData.uen);
    body.set('tax_number', formData.tax_number);
    body.set('contact_number', formData.contact_number);
    body.set('website', formData.website);
    body.set('address', formData.address);
    body.set('start_date', sdate);
    body.set('latitude', '20.25');
    body.set('latitude', '21.25'); */

    this.myService.postData('clients/' + this.client_id, form).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.getClientDetails();
            this.toast.showSuccess('Client Updated Successfully!.');
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
  }

}
